import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, merge, of, fromEvent } from 'rxjs';
import { distinctUntilChanged, map } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  title = 'my-portfolio';
  isConnected$: Observable<boolean>;
  showVar: boolean = false;

  // https://naum.studio/contact/
  // https://sii.im/#!/skills

  constructor(private router: Router) { }

  ngOnInit() {
    this.isConnected$ = this.checkConnection(window, navigator);
    const menu = document.querySelector('.menu');
    if (menu) this.startHamanimnation(menu);
  }

  checkConnection(window: Window, navigator: Navigator): Observable<boolean> {
    if (!!window.addEventListener && !!navigator) {
      return merge(
        of(navigator.onLine),
        fromEvent(window, 'online').pipe(map(() => true)),
        fromEvent(window, 'offline').pipe(map(() => false)),
      ).pipe(distinctUntilChanged());
    }
    return of(true);
  }

  toggleChild() {
    this.showVar = !this.showVar;
  }

  startHamanimnation(menu) {
    menu.addEventListener('click', () => {
      if (menu.classList.contains('open')) {
        menu.classList.remove('open');
        menu.classList.add('close');
        this.router.navigateByUrl('/home');
      } else {
        menu.classList.remove('close');
        menu.classList.add('open');
        this.router.navigateByUrl('/menu');
      }
    })
  }

  get showFooter() {
    return this.router.url.includes('/menu');
  }
}
